.like {
  &__title {
    font-weight: 700;
    font-size: 12px;
    line-height: 15px;
    text-transform: uppercase;
    color: #1f2041;
  }
  &__box {
    width: 40px;
    height: 20px;
    border: 1px solid rgba(31, 32, 65, 0.25);
    box-sizing: border-box;
    border-radius: 10px;
  }
  &__heart {
    width: 20px;
    height: 20px;
  }
  &__count {
    font-size: 10px;
    line-height: 12px;
    color: rgba(31, 32, 65, 0.25);
  }
}
